import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import Home from "../page/home.vue"
import Top250 from "../page/top250.vue"
import InTheaters from "../page/in-theater.vue"
import ComingSoon from "../page/coming-soon.vue"
import Weekly from "../page/weekly.vue"
import NoPage from "../page/no-page.vue"
import Details from "../page/detail.vue"
import User from "../page/user.vue"
import Login from "../page/login.vue"
import Reg from "../page/register.vue"

let routes = [{
		name: "home",
		path: "/home",
		component: Home
	},
	{
		name: "top250",
		path: "/top250",
		component: Top250
	},
	{
		path: "/inTheaters",
		component: InTheaters
	},
	{
		path: "/comingSoon",
		component: ComingSoon
	},
	{
		path: "/weekly",
		component: Weekly
	},
	{
		name:"details",
		path: "/details/:id",
		component: Details,
		props: route => ({
			id: route.params.id,
			...route.query
		})
	},
	{
		path: "/user",
		component: User
	},
	{
		path: "/login",
		component: Login
	},
	{
		path: "/reg",
		component: Reg
	},
	{
		path: "/",
		redirect: "/home"
	},
	{
		path: "*",
		component: NoPage
	}
]


let router = new VueRouter({
	mode: 'history',
	routes
})
export default router;
