<template>
	<div class="register">
		用户名<input type="text" name="user" v-model="username" /><br />
		密码<input type="text" name="pass" v-model="password" /><br />
		{{mess}}<br />
		<input type="button" value="注册" @click="reg">
	</div>
</template>

<script>
	import axios from "axios";
	export default {
		data() {
			return {
				username: "",
				password: "",
				mess:""
			}
		},
		methods: {
			reg() {
				let formdata = new FormData();
				formdata.append('username', this.username)
				formdata.append('password', this.password)
				axios({
					url: '/api/reg',
					method: 'post',
					data: formdata
				}).then(
					res => {
						if (res.data.err == 0) {
							console.log(res.data);
							this.$router.push('/login')
						} else {
							this.mess = res.data.msg
						}
					}
				)
			}
		}
	}
</script>

