<template>
	<div class="coming-soon">
		<h3>
			<p>即将上映</p>
			<router-link to="/comingSoon" tag="span">更多</router-link>
		</h3>
		<ul>
			<router-link tag="li" :to="'/details/'+items.id+'?dataName=coming_soon'" :canshu="items" v-for="(items, index) in data.subjects" :key="items.id">
				<Cell v-if="items.images" :data="items"></Cell>
			</router-link>
		</ul>
	</div>
</template>
<script>
	import Cell from "./cell.vue"
	// import Loading from "./loading.vue"
	export default {
		data() {
			return {
				data: {}
			}
		},
		components: {
			Cell,
			// Loading
		},
		mounted() {
			this.$axios({
				url: "https://douban-api.now.sh/v2/movie/coming_soon",
				// target: 'https://douban.uieee.com',
				// target: 'https://douban-api.uieee.com',
				// target: 'https://douban-api.now.sh',
				// target: 'https://douban-api.zce.now.sh',
				params: {
					start: 0,
					count: 3
				}
			}).then(
				res => this.data = res.data
			)
		}
	}
</script>

