<template>
	<div class="login-box">
		用户名<input type="text" name="user" v-model="username" /><br/>
		密码<input type="text" name="pass" v-model="password" /><br/>
		{{mess}}<br/>
		<input type="button" value="登录" @click="login">
	</div>
</template>

<script>
	
	export default {
		name:"login",
		data() {
			return {
				username: "",
				password: "",
				mess: ""
			}
		},
		methods: {
			login() {
				let params = new URLSearchParams();
				params.append('username', this.username)
				params.append('password', this.password)
				console.log(params);
				this.$axios({
					url: '/api/login',
					method: 'post',
					data: params
				}).then(
					res => {
						if (res.data.err == 0) {
							//种token,跳转user
							window.localStorage.setItem('token', res.data.token);
							this.$router.push('/user')
						} else {
							this.mess = res.data.msg
						}
					}
				)
			}
		}
	}
</script>

<style>
	.login-box{margin-top: 100px;}
	input{width: 100px; height: 30px; border: 1px solid #ccc;}
</style>
